.encabezado
    padding: 10px 
    background-color: $primary-color
    border-radius:5px 
    height: 10rem 
    min-width: 100rem  
    width: 100% 
    justify-content: space-around
    align-content: flex-center

.logo  
    width: auto 
    height: auto 
    max-width: 10rem 
    max-height: 10rem 
    padding: 5px 
 

.navegador ul  
    display: inline-flex 
    align-items: center 
    color: $text-color
    font-family:'Roboto',sans-serif 
    font-size: large 

.navegador ul li
    display: inline-block
    padding:20px 10px